let NumberStyleMixin = {
  methods: {
    negativeNumberStyle (value) {
      return value === 0 ? '' : (value < 0 ? 'text-danger' : 'text-success')
    },
    positiveNumberStyle (value) {
      return value === 0 ? '' : (value > 0 ? 'text-danger' : 'text-success')
    }
  }
}

export default NumberStyleMixin
