let LoadingMixin = {
  data: function () {
    return {
      loaders: {}
    }
  },
  methods: {
    startLoading (containerId) {
      if (this.loaders[containerId]) {
        this.loaders[containerId] = ++this.loaders[containerId]
      } else {
        this.loaders[containerId] = 1
        if (this.$refs.hasOwnProperty(containerId)) {
          this.$vs.loading({ container: this.$refs[containerId] })
        } else if (document.getElementById(containerId)) {
          this.$vs.loading({ container: '#' + containerId })
        }
      }
    },
    stopLoading (containerId) {
      if (this.loaders[containerId]) {
        this.loaders[containerId] = --this.loaders[containerId]
        if (this.loaders[containerId] === 0) {
          if (this.$refs.hasOwnProperty(containerId)) {
            this.$vs.loading.close(this.$refs[containerId])
          } else if (document.getElementById(containerId)) {
            this.$vs.loading.close('#' + containerId + ' > .con-vs-loading')
          }
        }
      }
    }
  }
}

export default LoadingMixin
