import Endpoint from '@/services/Endpoint'

export default {
  accounts (payload) {
    return Endpoint().get('/s1/connect/account', {
      params: payload
    })
  },
  transactions (payload) {
    return Endpoint().get(`/s1/connect/account/${payload.accountNumber}/transaction`, {
      params: payload
    })
  },
  modifyNickname (payload) {
    return Endpoint().patch('/s1/connect/account/nickname', payload)
  },
  officialStatements (payload) {
    return Endpoint().get(`/s1/connect/account/${payload.accountNumber}/statement/official`, {
      params: payload
    })
  },
  mt940Statements (payload) {
    return Endpoint().get(`/s1/connect/account/${payload.accountNumber}/statement/mt940`, {
      params: payload
    })
  },
  transactionTypes (payload) {
    return Endpoint().get('/s1/connect/account/transactionType', {
      params: payload
    })
  },
  accountTransactionAdvice (payload) {
    return Endpoint().get(`/s1/connect/account/${payload.accountNumber}/transaction/advice`, {
      params: payload
    })
  }
}
