<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
    </div>
    <div id="loading-container" class="nv-row row hover vs-con-loading__container">
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('TRANSACTION_ID') }}:</span>
          <span>{{ transaction.transactionId | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TRANSACTION_DESCRIPTION') }}:</span>
          <span>{{ transaction.narrative | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LOAN_NUMBER') }}:</span>
          <span>{{ transaction.accountNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="transaction.drCrIndicator === 'D'">
          <span>{{ $t('DEBIT_AMOUNT') }}:</span>
          <span>{{ transaction.amount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="transaction.drCrIndicator === 'C'">
          <span>{{ $t('CREDIT_AMOUNT') }}:</span>
          <span>{{ transaction.amount | currency('') | placeholder('-') }}</span>
        </p>
      </div>

      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row" v-if="transaction.currencyAbbreviation">
          <span>{{ $t('CURRENCY') }}:</span>
          <span>{{ transaction.currencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('BALANCE') }}:</span>
          <span :class="negativeNumberStyle(transaction.accountBalance)">{{ transaction.accountBalance | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('POSTING_DATE') }}:</span>
          <span>{{ transaction.postingDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('VALUE_DATE') }}:</span>
          <span>{{ transaction.valueDate | formatUnixDate }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import AccountService from '@/services/account/AccountService'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import AlertMixin from '@/mixins/commons/AlertMixin'

export default {
  name: 'loan-transaction-view',
  mixins: [NumberStyleMixin, LoadingMixin, AlertMixin],
  data: function () {
    return {
      transaction: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      AccountService.transactions({
        transactionId: this.$route.params.id,
        accountNumber: this.$route.params.accountNumber,
        fromDate: this.$route.params.fromDate,
        toDate: this.$route.params.toDate,
        orderByField: this.orderByField
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.transaction = response.data.transactionList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.transactionTableBusy = false
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
